// W3 Slider
var carouselchk = document.getElementById("carousel");

if(carouselchk){

  switch (document.readyState) {
    case 'loading':
      // The document is still loading.
      break
    case 'interactive':

      // The document has finished loading. We can now access the DOM elements.
      // But sub-resources such as images, stylesheets and frames are still loading.
      let span = document.createElement('span')
      span.textContent = 'A <span> element.'
      document.body.appendChild(span)
      break
    case 'complete':
      // The page is fully loaded.
      console.log('The first CSS rule is: ' + document.styleSheets[0].cssRules[0].cssText)
      break
  }

  const plusDivs = (n) => {
    showDivs(slideIndex += n)
  }

  const currentDiv = (n) => {
    showDivs(slideIndex = n)
  }

  const showDivs = (n) => {
    let i
    let x = document.getElementsByClassName('mySlides')
    let dots = document.getElementsByClassName('demo')

    if (n > x.length) slideIndex = 1

    if (n < 1) slideIndex = x.length

    for (i = 0; i < x.length; i++) {
      x[i].style.display = 'none'
    }

    for (i = 0; i < dots.length; i++) {
      dots[i].className = dots[i].className.replace(' w3-white', '')
    }

    x[slideIndex - 1].style.display = 'block'
    dots[slideIndex - 1].className += ' w3-white'
  }

  let slideIndex = 1
  showDivs(slideIndex)

  let plusDivPrev = document.getElementById('plusDivsPrev')
  let plusDivsNext = document.getElementById('plusDivsNext')
  let badgesPosition = document.getElementsByClassName('w3-badge')

  plusDivPrev.addEventListener('click', () => plusDivs(-1))
  plusDivsNext.addEventListener('click', () => plusDivs(1))

  for (let i = 0; i < badgesPosition.length; i++) {
    badgesPosition[i].addEventListener('click', () => currentDiv(i + 1))
  }
}
